<template>
    <div>
        <v-btn
            class="mx-2 right_20 top_85 mr-0 z-L"
            fab
            
            :color="color"
            :class="extra_calss"

            :fixed="is_fixed"

            :left="l_or_r"
            :right="!l_or_r"
            :x-small="is_small"

            :disabled="useless"

            @click="lockPrint"
        >
            <v-icon>
                {{ icon }}
            </v-icon>
        </v-btn>
        <div class="pdf-fixed-iframe" id="_data">
            <!-- pdf-user></pdf-user-->
            <iframe :src="link">
            </iframe>
        </div>

    </div>
</template>

<script>
import PdfUser from '../PdfUser/PdfUser.vue'
    export default {
  components: { PdfUser },
        name: '',
        props: {

            icon: {
                type: String,
                default: 'mdi-file-pdf-box-outline'
            },
            color: {
                type: String,
                default: ''
            },
            extra_calss: {
                type: String,
                default: ''
            },
            is_fixed: {
                type: Boolean,
                default: true
            },
            l_or_r: {
                type: Boolean,
                default: false
            },
            is_small: {
                type: Boolean,
                default: false
            },

            useless: {
                type: Boolean
            }
        },
        data() {
            return {
                print: false,

                link: 'https://image.baidu.com/search/detail?ct=503316480&z=undefined&tn=baiduimagedetail&ipn=d&word=%E4%BA%8C%E6%AC%A1%E5%85%83&step_word=&ie=utf-8&in=&cl=2&lm=-1&st=undefined&hd=undefined&latest=undefined&copyright=undefined&cs=2946084188,580717472&os=2058904330,31810149&simid=0,0&pn=23&rn=1&di=1320&ln=50&fr=&fmq=1622446669456_R&fm=&ic=undefined&s=undefined&se=&sme=&tab=0&width=undefined&height=undefined&face=undefined&is=0,0&istype=0&ist=&jit=&bdtype=0&spn=0&pi=0&gsm=0&hs=2&objurl=https%3A%2F%2Fgimg2.baidu.com%2Fimage_search%2Fsrc%3Dhttp%253A%252F%252F09.imgmini.eastday.com%252Fmobile%252F20180115%252F20180115193749_1eeb9a0c896b11750318d01c6eda7dc9_3.jpeg%26refer%3Dhttp%253A%252F%252F09.imgmini.eastday.com%26app%3D2002%26size%3Df9999%2C10000%26q%3Da80%26n%3D0%26g%3D0n%26fmt%3Djpeg%3Fsec%3D1625038669%26t%3D00536821552790df6eea28733810c93f&rpstart=0&rpnum=0&adpicid=0&force=undefined&ctd=1622446685432^3_921X917%1'
            }
        },
        methods: {
            lockPrint() {
                this.link = ''
                const uri = this.conf.baseURL + '/#/ui/pdf_user/?menu=false&unid=' + this.$store.state.unid
                this.link = uri
            }
        },
    }
</script>

<style lang="sass" scoped>
.pdf-fixed-iframe
    position: fixed
    top: 120vh
    left: 0
    z-index: 999
    width: 100vw
    height: 30vh !important
    overflow: scroll
    visibility: hidden

.pdf-fixed-iframe iframe
    width: 100%
</style>


